import * as React from 'react';import clsx from 'clsx';import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';import Drawer from '@material-ui/core/Drawer';import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';import Toolbar from '@material-ui/core/Toolbar';import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';import Divider from '@material-ui/core/Divider';import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';import Container from '@material-ui/core/Container';import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';import Link from '@material-ui/core/Link';import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';import NotificationsIcon from '@material-ui/icons/Notifications';
import { mainListItems, secondaryListItems } from './listItems';
import Chart from './Chart';import Deposits from './Deposits';import Orders from './Orders';
// import { spacing } from '@material-ui/system';

function Copyright(props) {return (
<Typography variant="body2" color="textSecondary" align="center" {...props}>{'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">Your Website</Link>{' '}{new Date().getFullYear()}{'.'}
</Typography>  );}

const drawerWidth = 240,szAppTitFix=22;

const useStyles = makeStyles((theme) => ({
  toolbar: {paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {display: 'flex', alignItems: 'center', justifyContent: 'flex-end', padding: '0 8px', ...theme.mixins.toolbar,},
  appBar: { zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {easing: theme.transitions.easing.sharp, duration: theme.transitions.duration.leavingScreen, }), },
  appBarShift: {marginLeft: drawerWidth, width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {easing: theme.transitions.easing.sharp, duration: theme.transitions.duration.enteringScreen,}), },
  menuButton: {marginRight: 36,}, menuButtonHidden: {display: 'none',},
  drawerPaper: { position: 'relative', whiteSpace: 'nowrap', width: drawerWidth, boxSizing: 'border-box',
    transition: theme.transitions.create('width', {easing: theme.transitions.easing.sharp, duration: theme.transitions.duration.enteringScreen,  }),  },
  drawerPaperClose: {overflowX: 'hidden',   width: theme.spacing(7), [theme.breakpoints.up('sm')]: {width: theme.spacing(9), },
    transition: theme.transitions.create('width', {easing: theme.transitions.easing.sharp, duration: theme.transitions.duration.leavingScreen,   }), },
  appBarSpacer: theme.mixins.toolbar,
}));

export default function Dashboard() {const classes = useStyles(); const [open, setOpen] = React.useState(true); const toggleDrawer = () => {setOpen(!open); };

  return (<Box sx={{ display: 'flex' }}><CssBaseline />
      <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)} >
        <Toolbar className={classes.toolbar}>
          <IconButton edge="start" color="inherit" aria-label="open drawer" onClick={toggleDrawer}
            className={clsx(classes.menuButton, open && classes.menuButtonHidden)} >       <MenuIcon />         </IconButton>
          <Typography component="h1" variant="h6" color="inherit" noWrap sx={{ flexGrow: 1, paddingLeft: szAppTitFix }} >&nbsp;szDashboard </Typography>
          <IconButton color="inherit"><Badge badgeContent={4} color="secondary"><NotificationsIcon /></Badge></IconButton>        </Toolbar></AppBar>
      <Drawer variant="permanent" classes={{paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose), }} open={open} >
        <div className={classes.toolbarIcon}><IconButton onClick={toggleDrawer}><ChevronLeftIcon /></IconButton></div>
        <Divider /><List>{mainListItems}</List><Divider /><List>{secondaryListItems}</List></Drawer>
      <Box component="main" sx={{backgroundColor: (theme) => theme.palette.mode === 'light' ? theme.palette.grey[100]:theme.palette.grey[900],
          flexGrow: 1,  height: '100vh', overflow: 'auto', }} >
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
          <Grid container spacing={3}>
		    <Grid item xs={12} md={8} lg={9}>{/* Chart */}
              <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column', height: 240 }} ><Chart />   </Paper></Grid>
            <Grid item xs={12} md={4} lg={3}>{/* Recent Deposits */}
              <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column', height: 240 }} ><Deposits /></Paper></Grid>
            <Grid item xs={12}>{/* Recent Orders */}
              <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column' }}>                <Orders /></Paper></Grid>          </Grid>
          <Copyright sx={{ pt: 4 }} />
        </Container></Box></Box>);}
